import { useEffect, useState } from "react";

const fetchImg = async () => {
  const text = await fetch("https://jsonplaceholder.typicode.com/comments/1");
  const json = await text.json();
  return json;
  //console.log(text);
};

export default function ImgItem() {
  const [data, setData] = useState([]);

  useEffect(() => {
    const dispImg = async () => {
      const img = await fetchImg();
      setData(img);
    };
    dispImg();
  }, []);

  return (
    <>
      <p>
        Name:{data.name} <br></br>Email: {data.email}
        <br></br> Comment:{data.body}
      </p>
    </>
  );
}
