import React, { useState } from "react";
import AdminSide from "./AdminSide";

export default function PassingProps({ pprops, setPprops }) {
  const [newPprops, setNewPprops] = useState("");

  return (
    <div>
      Props:
      <input onChange={(e) => setNewPprops(e.target.value)}></input>
      <button onClick={() => setPprops(newPprops)}>Change pprops</button>
      {pprops}
    </div>
  );
}
